import React from "react";
import { COLORS } from "@/app/constants";

interface ButtonProps {
  isMain?: boolean;
  text: string;
  onClick: () => void;
}

const Button: React.FC<ButtonProps> = ({ isMain, text, onClick }) => {
  const buttonStyle: React.CSSProperties = {
    backgroundColor: isMain ? COLORS.BRAND_PRIMARY : "transparent",
    color: isMain ? "white" : COLORS.BRAND_PRIMARY,
    border: isMain ? "none" : `1px solid ${COLORS.BRAND_PRIMARY}`,
  };

  return (
    <button
      className={`flex justify-center items-center w-full py-4 rounded-lg`}
      style={buttonStyle}
    >
      {text}
    </button>
  );
};

export default Button;
